import React from 'react';
import './Navbar.css'

class Navbar extends React.Component{
    render(){
        return (
        <div className="Navbar">
            <div className="list">
                
                <ul>
                    <li className="list-links">
                    <button className="about" onClick={this.props.click}>About</button>
                    </li>
                    <li className="list-links">
                    <button className="projects" onClick={this.props.clickPrj}>Projects</button>
                    </li>            
                    <li className="list-links">
                    <a href="https://my.indeed.com/p/leq285w">Resume</a>
                    </li>
                    <li className="list-links">
                    <button onClick={this.props.clickContact}>Contact</button>
                    </li>
                    
                </ul>
                <div className="underline" />  
            </div>
                
         </div>
        )
    }
}

export default Navbar;