import React from 'react';
import './Projects.css';
import calculatorImg from "./calculatorImg.png"


class Projects extends React.Component{   
  
calcLink(){
    window.location.href="http://Aldin38.github.io/Calculator"
}
    render(){
    return(
    <div className="projects-main">
    
        <h2>My Projects</h2>

          <div className="project1">

            <h2>Calculator</h2>
            <img className="calc-img" src={calculatorImg} alt="calculator"></img>
            <div className="button-wrapper">
            
            <button classname="projects-buttons" onClick={this.calcLink.bind(this)}>View Project</button>
               
            <button classname="projects-buttons">View Code</button>
            </div>

         </div>

     
    </div>
    )
}
}
export default Projects;