import React from 'react';
import Modal from 'react-modal'
import './Modals.css';
import Navbar from '../Navbar/Navbar';
import Projects from './Projects';
import About from './About.js'
import Contact from './Contact';

class Modals extends React.Component{
    constructor(props){
        super(props)
        this.state = {
         modalState: false,
         display: ''
        }
     this.handleClickAbout = this.handleClickAbout.bind(this)
    }
  
    handleClickContact(){
      this.setState({
        modalState: true,
        display: <Contact />
      })
   
    }
    
    handleClickProjects(){
      this.setState({
        modalState: true,
        display: <Projects/>
      })
    }

    handleClickAbout(){
        this.setState({
            modalState: true,
            display: <About />
        })
      }

      
    render()  {

 
   
    return(
       
<div className='wrapper'>
<Navbar click={this.handleClickAbout.bind(this)} clickPrj={this.handleClickProjects.bind(this)} clickContact={this.handleClickContact.bind(this)}  />
<Modal ariaHideApp={false}  style={{
    overlay: {
      zIndex: '502',
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'black 0.8'
    },
    content: {
      zIndex:'500',
      position: 'absolute',
      top: '40px',
      left: '40px',
      right: '40px',
      bottom: '40px',
      border: '#1A1818 solid 2px',
      background: '#1A1818',
      overflow: 'auto',
      WebkitOverflowScrolling: 'touch',
      borderRadius: '10px',
      outline: 'none',
      padding: '20px'
    }
  }} isOpen={this.state.modalState} shouldCloseOnOverlayClick={true} onRequestClose={() => this.setState({modalState:false})}>
   <div>
   <button onClick={() => this.setState({modalState:false,display:''})} className="btn btn-default">X</button>

  {this.state.display}
  
  </div>
</Modal>


</div>

    );  
}

}
export default Modals;